.cv-section {
  background: #0d0d0f;
  color: #f5f5ff;
  padding: 100px 0;
  overflow: hidden;
}

.cv-title {
  text-align: center;
  font-weight: 700;
  font-size: 2.8rem;
  color: #c084fc;
  margin-bottom: 10px;
}

.cv-subtitle {
  text-align: center;
  color: #a68ef5;
  font-size: 1rem;
  margin-bottom: 60px;
}

.cv-content {
  display: flex;
  justify-content: center;
  gap: 40px;
  flex-wrap: wrap;
}

.cv-left,
.cv-right {
  flex: 1;
  min-width: 320px;
  max-width: 500px;
}

/* Card Styling */
.cv-card {
  background: rgba(25, 10, 40, 0.45);
  border: 1px solid rgba(140, 60, 255, 0.3);
  border-radius: 25px;
  padding: 30px;
  margin-bottom: 30px;
  box-shadow: 0 0 20px rgba(140, 60, 255, 0.15);
  backdrop-filter: blur(10px);
  transition: all 0.4s ease;
}

.cv-card:hover {
  box-shadow: 0 0 35px rgba(170, 90, 255, 0.4);
  transform: translateY(-6px);
}

.cv-card h2 {
  color: #c084fc;
  font-size: 1.3rem;
  margin-bottom: 15px;
}

.cv-item {
  margin-bottom: 15px;
}

.cv-item h3 {
  color: #fff;
  font-weight: 600;
  margin-bottom: 4px;
}

.cv-item span {
  color: #c084fc;
}

.cv-date {
  color: #a68ef5;
  font-size: 0.9rem;
  margin-bottom: 6px;
}

.cv-card p {
  color: #d8ccff;
  line-height: 1.6;
}

/* Skills */
.cv-skills {
  list-style: none;
  padding: 0;
  margin: 0;
}

.cv-skills li {
  background: rgba(140, 60, 255, 0.15);
  border: 1px solid rgba(140, 60, 255, 0.25);
  border-radius: 15px;
  display: inline-block;
  padding: 6px 14px;
  margin: 5px;
  color: #e8e0ff;
  font-size: 0.9rem;
  transition: all 0.3s ease;
}

.cv-skills li:hover {
  background: rgba(180, 90, 255, 0.3);
  transform: scale(1.05);
}

/* Download Button */
.cv-download {
  text-align: center;
  margin-top: 40px;
}

.btn-download {
  background: linear-gradient(90deg, #7e22ce, #9333ea);
  color: #fff;
  border: none;
  border-radius: 25px;
  padding: 12px 28px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.4s ease;
}

.btn-download:hover {
  background: linear-gradient(90deg, #9333ea, #c084fc);
  box-shadow: 0 0 20px rgba(180, 90, 255, 0.4);
  transform: translateY(-3px);
}

/* Responsive */
@media (max-width: 768px) {
  .cv-title {
    font-size: 2rem;
  }
  .cv-content {
    flex-direction: column;
    align-items: center;
  }
}
